* {
  margin: 0;
  padding: 0; }

h1,
h2,
h3,
h4,
h5 {
  margin: 0px;
  padding: 0px;
  text-transform: uppercase; }

body {
  font-family: Century Gothic Std, Arial, sans-serif;
  background: #fff;
  text-align: center; }

.link {
  text-decoration: none;
  color: #000; }
  .link:hover {
    text-decoration: none;
    color: #000; }

a {
  text-decoration: none;
  color: #212529; }
  a:hover {
    text-decoration: none;
    color: #212529; }

ul {
  list-style: none; }

img {
  width: 100%; }

.icon {
  width: 1.3125rem;
  height: 1.375rem; }

.img-wrap {
  max-width: 15rem; }

.bg-grey {
  background: #f6f6f6; }

.cursor {
  cursor: pointer; }

.profile-icon {
  position: absolute;
  left: 0; }

.p-rel {
  position: relative; }

.row {
  margin: 0 !important; }

.border-secondary {
  border-color: #e5e5e5 !important; }

.main-container {
  max-width: 980px; }

.submit-button {
  display: flex;
  align-items: center;
  height: 2.5rem;
  padding: 0 1.1875rem 0 1.3125rem;
  border-radius: 1.5rem;
  border: 0.0625rem solid #e5e5e5;
  box-shadow: 0 0.0625rem 0.25rem 0 rgba(0, 0, 0, 0.25);
  box-sizing: border-box;
  outline: none;
  display: flex;
  color: #fff;
  background: #000;
  box-shadow: none; }
  .submit-button:hover {
    outline: none;
    font-weight: 700;
    box-shadow: 0 0.125rem 0.5rem 0 rgba(0, 0, 0, 0.25); }

.text-grey {
  color: #636363; }
  .text-grey:hover {
    text-decoration: none; }

.text-black {
  color: #000; }

.bg-black {
  background: #000; }

.container {
  padding: 0 1.78125rem;
  max-width: 1800px; }

header {
  width: 100%;
  border-bottom: 0.0625rem solid #e5e5e5; }

.header-content {
  display: grid;
  grid-template-columns: repeat(9, 1fr); }
  .header-content > div {
    align-self: center; }

.header-left {
  display: flex; }

.header-left-search {
  grid-column: 2 / 5; }

.header-center {
  font-family: 'Crimson Text', serif;
  font-weight: bold;
  margin: 0;
  padding: 0;
  text-transform: uppercase; }

.header-right {
  grid-column: 6 / 10; }
  .header-right > div {
    padding: 0 1.5rem; }

.header-button {
  display: flex;
  align-items: center;
  background-color: #fff;
  height: 2.5rem;
  padding: 0 1.1875rem 0 1.3125rem;
  border-radius: 1.5rem;
  border: 0.0625rem solid #e5e5e5;
  box-shadow: 0 0.0625rem 0.25rem 0 rgba(0, 0, 0, 0.25);
  box-sizing: border-box;
  outline: none; }
  .header-button:hover {
    font-weight: 700;
    box-shadow: 0 0.125rem 0.5rem 0 rgba(0, 0, 0, 0.25); }

.header-content {
  min-height: 5rem; }

.header-brand {
  margin: 0;
  padding: 0; }

.header-search {
  max-width: 18.75rem; }

.header-form {
  width: 100%;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  border: 1px solid #757575;
  -webkit-transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  padding: 0.375rem 1rem;
  border-radius: 1.5rem; }
  .header-form:focus {
    border: 1px solid #757575;
    box-shadow: inset 0 0 0.0625rem 0.0625rem #757575;
    outline: none; }

.header-search-icon {
  position: absolute;
  right: 0;
  height: 100%;
  border: none;
  z-index: 1;
  background: rgba(0, 0, 0, 0);
  border-radius: 1.5rem; }
  .header-search-icon-input {
    background: #000;
    fill: #fff;
    color: #fff;
    border: 1px solid #757575;
    border-left: none;
    box-shadow: inset 0 0 0.0625rem 0.0625rem #757575; }

span {
  display: block; }

svg {
  width: 1.125rem;
  height: 0.875rem; }

.navbar-content {
  position: relative; }

.navbar-overlay {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
  cursor: pointer; }

.navbar-hidden {
  display: none;
  visibility: none;
  opacity: 0;
  z-index: -1; }

.navbar-sidebar {
  z-index: 6;
  position: fixed;
  left: -25.625rem;
  top: 0;
  width: 25.625rem;
  height: 100%;
  background: #fff; }
  .navbar-sidebar-show {
    left: 0rem !important; }
  .navbar-sidebar-close {
    height: 5rem;
    display: flex;
    padding: 0 1.78125rem;
    border-bottom: 1px solid #e5e5e5; }
  .navbar-sidebar-button {
    position: relative;
    width: 1.7rem;
    background: #fff;
    border: none;
    font-size: 2rem;
    outline: none !important;
    cursor: pointer; }
    .navbar-sidebar-button > div {
      width: 100%;
      height: 1px;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
      background: #495057; }
      .navbar-sidebar-button > div::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        background: #495057;
        width: 100%;
        height: 1px;
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg); }
  .navbar-sidebar-list {
    letter-spacing: 0.3rem; }
    .navbar-sidebar-list > ul > li {
      cursor: pointer; }

.slider-inner {
  background: #fff;
  position: relative;
  overflow: hidden;
  width: 100%; }

.slider-item {
  position: absolute;
  -webkit-transition: left 1s ease-in;
  transition: left 1s ease-in;
  width: 100%; }
  .slider-item-title {
    background: #fff; }

.slider-img-contianer {
  position: relative;
  overflow: hidden; }

.slider-mobile-img {
  display: none;
  visibility: hidden; }

.slider-left {
  left: -100%;
  z-index: 2; }

.slider-left-perv {
  left: -100%;
  opacity: 0; }

.slider-center {
  left: 0;
  z-index: 3; }

.slider-right {
  left: 100%; }

.slider-control-wrap {
  position: relative; }

.slider-control {
  z-index: 4;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between; }
  .slider-control > button {
    background: rgba(0, 0, 0, 0);
    border: none;
    padding: 0 3rem;
    outline: none; }

.slider-count-list {
  z-index: 3;
  position: absolute;
  right: 0px;
  bottom: -2.2rem;
  display: flex;
  justify-content: flex-end; }

.slider-count-item {
  background: #e5e5e5;
  margin-right: 1rem;
  border-radius: 50%;
  width: 0.75rem;
  height: 0.75rem;
  -webkit-transition: -webkit-transform 0.3s ease-in;
  transition: -webkit-transform 0.3s ease-in;
  transition: transform 0.3s ease-in;
  transition: transform 0.3s ease-in, -webkit-transform 0.3s ease-in;
  cursor: pointer; }

.slider-count-active {
  background: #495057;
  -webkit-transform: scale(1.4);
          transform: scale(1.4); }

.section-recomand {
  background: #f6f6f6; }

.section-male {
  border-bottom: 1px solid #e5e5e5; }

.contact {
  color: #000; }
  .contact > ul > li > a {
    color: #000 !important; }

.register-form-group {
  border: 1px solid #e5e5e5; }

.create-user-link {
  border-left: 1px solid #e5e5e5; }

.form-input-item {
  padding: 10px 0;
  width: 100%; }
  .form-input-item > input {
    width: 100%;
    background: #e5e5e5;
    font-size: 0.8rem;
    outline: none;
    border: none;
    border-radius: 5px;
    padding: 6px 10px; }

.invalid-feedback {
  display: none;
  color: #df280a; }

.form-input-error > input {
  border: 1px solid #df280a; }

.form-input-error .invalid-feedback {
  display: block; }

.select-error {
  border: 1px solid #df280a !important; }

.btn-outline-dark {
  color: #000;
  border-color: #000;
  border-radius: 1.5rem; }
  .btn-outline-dark:hover {
    color: #fff;
    background: #000; }

.validate-select {
  margin: 1rem 0;
  width: 100%;
  padding: 0.5rem 1.18rem;
  border: 0.0625rem solid #e5e5e5;
  box-shadow: 0 0.0625rem 0.25rem 0 rgba(0, 0, 0, 0.25);
  box-sizing: border-box;
  outline: none;
  display: flex;
  box-shadow: none; }

.address-input {
  width: 50% !important; }

.alert {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  padding: 10px 0;
  color: #000; }

.items-column {
  width: 31.5%; }

.items-wrap {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); }

.items-wrap {
  max-width: 1200px; }

.detail-wrap {
  position: relative; }

.detail-img-wrap {
  max-width: 300px; }

.item-detail-collapse {
  width: 100%;
  background: #fff;
  display: none;
  position: absolute; }

.show {
  display: block; }

.item-wrap-single {
  max-width: 200px; }

.cart-item-wrap {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-areas: 'img name button sum'; }

.cart-item-img {
  grid-area: img;
  max-width: 100px; }

.cart-item-name {
  grid-area: name; }

.cart-item-button {
  grid-area: button; }

.cart-item-sum {
  grid-area: sum; }

.spinner-overlay {
  top: 0;
  left: 0;
  z-index: 10000;
  background: rgba(0, 0, 0, 0);
  position: fixed;
  color: #fff; }

.spinner-wrap {
  width: 5rem;
  height: 5rem;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 1rem; }

.landing-date {
  font-size: 1rem; }

@media (max-width: 992px) {
  .header-content {
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: repeat(2, 1fr); }
  .header-checkbox {
    width: 2.5rem !important; }
  .header-left {
    grid-column: 1 / 5 !important; }
  .header-left-search {
    padding: 0.5rem 0 1rem 0;
    grid-row: 2 / 3;
    grid-column: 1 / 10 !important; }
  .header-search {
    max-width: 100rem !important; }
  .header-button {
    padding: 0;
    width: 2.5rem;
    border-radius: 50%;
    justify-content: center; }
  .header-button-title {
    display: none; }
  .header-right > div {
    padding: 0 1rem !important; }
  .navbar-sidebar {
    left: -100%;
    width: 100% !important; }
  .navbar-sidebar-close {
    justify-content: center; }
  .slider-img {
    display: none !important; }
  .slider-mobile-img {
    display: block;
    visibility: visible; }
  .slider-control > button {
    padding: 1rem;
    display: flex;
    align-items: flex-start; }
  .slider-item-mobile-title {
    font-size: 1rem; }
  .slider-button {
    /* padding: 0; */
    /* width: 2.5rem; */
    /* border-radius: 50%; */
    /* justify-content: center; */ }
  .create-user-link {
    border: none; }
  .not-profile-aside {
    display: none; }
  .address-input {
    width: 100% !important; }
  .cart-item-wrap {
    display: grid;
    font-size: 130%;
    grid-template-areas: 'img img name name' 'img img  button button' 'img img  sum sum';
    grid-template-columns: 1fr 1fr 1fr 1fr; }
  .user-info {
    display: none; } }

